/* ----- blinking cursor animation ----- */
.typed-cursor{
  opacity: 1;
  -webkit-animation: blink 0.95s infinite;
  -moz-animation: blink 0.95s infinite;
  -ms-animation: blink 0.95s infinite;
  -o-animation: blink 0.95s infinite;
  animation: blink 0.95s infinite;
}

@-keyframes blink{
  0% { opacity:1; }
  50% { opacity:0; }
  100% { opacity:1; }
}
@-webkit-keyframes blink{
  0% { opacity:1; }
  50% { opacity:0; }
  100% { opacity:1; }
}
@-moz-keyframes blink{
  0% { opacity:1; }
  50% { opacity:0; }
  100% { opacity:1; }
}
@-ms-keyframes blink{
  0% { opacity:1; }
  50% { opacity:0; }
  100% { opacity:1; }
}
@-o-keyframes blink{
  0% { opacity:1; }
  50% { opacity:0; }
  100% { opacity:1; }
}



/* -------------------------------- 

Main content

-------------------------------- */


.cd-main {
	overflow-x: hidden;
	margin-top: -20px;
	position: relative;	

	&::before {
		/* never visible - this is used in jQuery to check the current MQ */ 
		display: none;
		content: 'mobile';
	} 

	> * {
		@include transition(transform $fold-animation $fold-content-animation);
	}
	
	&.fold-is-open > * {
		/* on mobile - translate .cd-main content to the right when the .cd-folding-panel is open */
		@include transform(translateX(100%));
		@include transition(transform $fold-animation 0s);
	}

	@include MQ(M) {

		&.fold-is-open > * {
			@include transform(translateX(600px));
		}
	}

	@include MQ(L) {

		&::before {
			/* never visible - this is used in jQuery to check the current MQ */ 
			content: 'desktop';
		}

		&.fold-is-open > * {
			/* reset style - on bigger devices we translate the gallery items */
			@include transform(translateX(0));
		}
	}
}

.cd-gallery {
	@include clearfix;
	list-style: none;
	margin: 0;
	padding: 0;
	&::before {
		/* this is the dark layer covering the .cd-gallery when the .cd-folding-panel is open */
		display: block;
		content: '';
		position: absolute;
		z-index: 1;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		background-color: rgba($color-5, .6);
		opacity: 0;
		visibility: hidden;
		@include transition(opacity  $fold-animation $fold-content-animation, visibility 0s $fold-animation+$fold-content-animation);
	}

	.fold-is-open &::before {
		visibility: visible;
		opacity: 1;
		@include transition(opacity  $fold-animation 0s, visibility 0s 0s);
	}
}

