/* -------------------------------- 

folding panel

-------------------------------- */

.cd-folding-panel {
	position: fixed;
	z-index: 1;
	top: 0;
	left: 0;
	height: 100vh;
	width: 100%;
	visibility: hidden;
	overflow: hidden;
	
	@include transition(visibility 0s $fold-animation+$fold-content-animation);

	.fold-left,
	.fold-right {
		/* the :after elements of .fold-left and .fold-right are the 2 fold sides */
		width: 100%;
		height: 100vh;
		overflow: hidden;
		position: relative;

		/* enable a 3D-space for children elements */
		@include perspective(2000px);

		&::after {
			/* 2 fold sides */
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			width: 100%;

			/* Force Hardware Acceleration */
			@include transform(translateZ(0));
			@include backface-visibility(hidden);

			@include transition(transform $fold-animation $fold-content-animation, background-color $fold-animation $fold-content-animation);
		}
	}

	.fold-right {
		@include perspective-origin(0% 50%);

		&::after {
			@include transform-origin(right center);
			@include transform(translateX(-100%) rotateY(-90deg));
			background-color: darken($color-3, 10%);
		}
	}

	.fold-left {
		/* on mobile only the right fold side is visible */
		display: none;

		&::after {
			background-color: lighten($color-3, 12%);
		}
	}

	.cd-close {
		/* 'X' close icon */
		position: absolute;
		z-index: 1;
		display: inline-block;
		top: 10px;
		right: 10px;
		height: 44px;
		width: 44px;

		/* image replacement */
		overflow: hidden;
		text-indent: 100%;
		white-space: nowrap;

		opacity: 0;
		@include transition(opacity .2s 0s);

		&::after, 
		&::before {
			/* lines of 'X' icon */
			content: '';
			position: absolute;
			height: 3px;
			width: 32px;
			left: 50%;
	    	top: 50%;
			background-color: $color-2;	
			@include backface-visibility(hidden);
			@include transition(opacity .2s);
		}

		&::after {
			@include transform(translateX(-50%) translateY(-50%) rotate(45deg));
		}

		&::before {
			@include transform(translateX(-50%) translateY(-50%) rotate(-45deg));
		}
	}

	&.is-open {
	background: radial-gradient(ellipse at center, rgba(255,232,209,1) 0%,rgba(255,207,160,1) 53%,rgba(255,189,109,1) 100%);		
		visibility: visible;
		@include transition(visibility 0s 0s);

		.fold-right::after,
		.fold-left::after {
			@include transform(translateX(0));
			@include transition(transform $fold-animation 0s, background-color $fold-animation 0s);
		}

		.fold-right::after {
			background-color: lighten($color-3, 12%);
		}

		.cd-close {
			opacity: 1;
			@include transition(opacity .2s $fold-animation);
		}
	}

	@include MQ(M) {
		width: 600px;
	}

	@include MQ(L) {
		@include center(x);
		width: 800px;

		.fold-left,
		.fold-right {
			@include column(.5);
			height: 100%;
		}

		.fold-right {
			/* change perspective-origin so that the 2 fold sides have the same vanishing point */
			@include perspective-origin(0% 50%);

			&::after {
				@include transform-origin(right center);
				@include transform(translateX(-100%) rotateY(-90deg));
			}
		}

		.fold-left {
			display: block;
			/* change perspective-origin so that the 2 fold sides have the same vanishing point */
			@include perspective-origin(100% 50%);

			&::after {
				@include transform-origin(left center);
				@include transform(translateX(100%) rotateY(90deg));
			}
		}
	}
}

.cd-fold-content {
	background: radial-gradient(ellipse at center, rgba(255,232,209,1) 0%,rgba(255,207,160,1) 53%,rgba(255,189,109,1) 100%);
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	padding: 4em 2em;

	visibility: hidden;
	@include transition(visibility 0s $fold-content-animation);

	> * {
		opacity: 0;
		@include transform(translateY(20px));
	}
	
	h2 {
