		font-size: 36px;
		text-transform: uppercase;
		margin-bottom: 40px;
		text-align: center;

		@include transition(transform $fold-content-animation/2 $fold-content-animation/2, opacity $fold-content-animation/2 $fold-content-animation/2);
	}

	h3 {
